.body {
  margin: 0;
}

.main {
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

@keyframes rotate-hand {
  from {
    transform: translateX(-50%)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg);
  }
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  z-index: 1;
  background-color: #000;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-hand;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  z-index: 2;
  background-color: #000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-hand;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.stopwatch__center {
  z-index: 3;
  background-color: red;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation-duration: 10s;
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  animation-duration: 600s;
}
